import React, { ReactNode } from "react";
import { Box } from "@mui/material";

interface RouteRenderViewWrapperProps {
  children: ReactNode;
}

const RouteRenderViewWrapper: React.FC<RouteRenderViewWrapperProps> = ({
  children,
  ...rest
}) => {
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        flex: 1,
        maxWidth: { xl: 1650 },
        mx: { xl: "auto" },
        width: { xl: "100%" },
      }}
      {...rest}
    >
      {children}
    </Box>
  );
};

export default RouteRenderViewWrapper;
